@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

div, img, h1, h2, h3, section, header, main, footer, nav {
  margin: 0;
  padding: 0;
}

.attention-text, .ordinary-text, .boldTitle-text {
  color: #fff;
  font-family: Poppins, Arial, Helvetica, sans-serif;
  font-weight: normal;
}

.attention-text {
  letter-spacing: -1.5px;
  font-size: 64px;
  font-weight: 500;
  line-height: 64px;
}

@media screen and (max-width: 1260px) {
  .attention-text {
    font-size: 48px;
    line-height: 48px;
  }
}

@media screen and (max-width: 744px) {
  .attention-text {
    font-size: 32px;
    line-height: 32px;
  }
}

.ordinary-text {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}

.boldTitle-text {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

.button-text {
  color: #1d1d1d;
  font-family: Poppins, Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
}

.inter-text {
  color: #fff;
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 34px;
}

.button__global {
  isolation: isolate;
  --x: 0;
  --y: 0;
  position: relative;
  overflow: hidden;
}

.button__global:after {
  content: "";
  top: var(--Y);
  left: var(--x);
  width: 500px;
  height: 500px;
  opacity: .6;
  z-index: -1;
  background: #2d0939;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%)scale(0);
}

.button__global:hover, .button__global:focus {
  cursor: pointer;
  box-shadow: 1px 4px 16px #e8e8f066;
}

.button__global:active {
  box-shadow: 1px 4px 16px #e8e8f0a3;
}

.rippleButton:after {
  animation: rippleButton .5s;
}

@keyframes rippleButton {
  100% {
    opacity: 0;
    transform: translate(-50%, -50%)scale(1);
  }
}

