@keyframes appear {
  0% {
    opacity: 0;
    left: -150%;
  }

  10% {
    left: 0;
  }

  20% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes disappear {
  0% {
    opacity: 1;
    left: 0;
  }

  90% {
    opacity: 0;
    left: 0;
  }

  95% {
    left: 0;
  }

  100% {
    left: -150%;
  }
}

.menuButton {
  width: 44px;
  height: 36px;
  z-index: 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menuButton:hover, .menuButton:focus {
  cursor: pointer;
}

.menuButton:hover .menuButton__bar--up, .menuButton:focus .menuButton__bar--up {
  width: 12px;
}

.menuButton:hover .menuButton__bar--down, .menuButton:focus .menuButton__bar--down {
  width: 16.8px;
}

.menuButton__content {
  width: 24px;
  height: 16px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.menuButton__bar {
  width: 24px;
  height: 3px;
  background-color: #000;
  border-radius: 3px;
  transition: all .5s ease-in-out;
  display: flex;
}

.menuButtonOpen .bar--up {
  width: 28.8px;
  transform-origin: 100% 0;
  transform: rotate(-45deg)translateY(-4px);
}

.menuButtonOpen .bar--mid {
  transition: all .1s ease-in-out;
  transform: scale(0);
}

.menuButtonOpen .bar--down {
  width: 28.8px;
  transform-origin: 100% 100%;
  transform: rotate(45deg)translateY(4px)translateX(1px);
}

.menuButtonOpen:hover, .menuButtonOpen:focus {
  cursor: pointer;
